<template>
  <!--头部菜单-->
  <el-menu class="el-menu-demo" mode="horizontal" background-color="#334157" text-color="#fff" active-text-color="#fff">
    <el-button class="buttonimg">
      <img class="showimg" :src="collapsed?imgsq:imgshow" @click="toggle(collapsed)">
    </el-button>
    <el-submenu index="2" class="submenu">
      <template slot="title">超级管理员</template>
      <el-menu-item index="2-1">设置</el-menu-item>
      <el-menu-item @click="content()" index="2-2">个人中心</el-menu-item>
      <el-menu-item @click="exit()" index="2-3">退出</el-menu-item>
    </el-submenu>
    <el-link><i class="iconfont  iconwentigaishu_m"></i></el-link>
    <el-link class="iconfont icontongzhi"></el-link>
    <el-popover
      placement="top-start" width="500"
      trigger="hover">
      <el-link slot="reference">日历<i></i></el-link>
      <el-calendar >
      </el-calendar>
    </el-popover>
    <el-link>扩展包<i></i></el-link>
    <el-link>免费试用<i class="iconfont iconhuangguan"></i></el-link>
  </el-menu>
</template>
<script>

  export default {
    name: 'navcon',
    data() {
      return {
        collapsed: true,
        imgshow: require('../assets/img/show.png'),
        imgsq: require('../assets/img/sq.png'),
        user: {}
      }
    },
    // 创建完毕状态(里面是操作)
    created() {
      this.user = JSON.parse(localStorage.getItem('userdata'))
    },
    methods: {
      // 退出登录
      exit() {
        this.$confirm('退出登录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(() => {
            setTimeout(() => {
              this.$store.commit('logout', 'false')
              this.$router.push({path: '/login'})
              this.$message({
                type: 'success',
                message: '已退出登录!'
              })
            }, 1000)
            // loginout()
            //   .then(res => {
            //     if (res.success) {
            //       //如果请求成功就让他2秒跳转路由
            //       setTimeout(() => {
            //         this.$store.commit('logout', 'false')
            //         this.$router.push({ path: '/login' })
            //         this.$message({
            //           type: 'success',
            //           message: '已退出登录!'
            //         })
            //       }, 1000)
            //     } else {
            //       this.$message.error(res.msg)
            //       this.logining = false
            //       return false
            //     }
            //   })
            //   .catch(err => {
            //     // 获取图形验证码
            //     this.getcode()
            //     this.logining = false
            //     this.$message.error('退出失败，请稍后再试！')
            //   })
          })
          .catch(() => {
            this.$message({
              type: 'info',
              message: '已取消'
            })
          })
      },
      // 切换显示
      toggle(showtype) {
        this.collapsed = !showtype
        this.$root.Bus.$emit('toggle', this.collapsed)
      }
    }
  }
</script>
<style scoped>
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    border: none;
  }

  .submenu {
    float: right;
  }

  .buttonimg {
    height: 60px;
    background-color: transparent;
    border: none;
  }

  .showimg {
    width: 26px;
    height: 26px;
    position: absolute;
    top: 17px;
    left: 17px;
  }

  .showimg:active {
    border: none;
  }

  .el-link {
    margin: 20px;
    color: white;
    float: right;
  }
</style>



